@charset "UTF-8";
input {
  background: #ffffffc4;
  padding: 16px 26px;
  border-width: 0;
  border-bottom: 2px solid #efefef;
  transition: all .4s;
  width: 100%;
  box-sizing: border-box;
  outline: none; }
  @media only screen and (max-width: 400px) {
    input {
      padding: 12px 15px; } }
  input:focus {
    border-bottom-color: #E887B8; }
  input.disabled {
    opacity: .4;
    border-bottom-color: #fff; }

::placeholder {
  color: rgba(72, 67, 110, 0.3); }
  ::placeholder ＆:first-letter {
    text-transform: uppercase; }
